header {
    padding-top : 5rem;
    overflow: hidden;
}

.header_container{
    text-align: center;
    height : 100%;
    position: relative;
}

/*================= CTA ==================*/
.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/*================= SOCIALS ==================*/

.header_socials{
    display:flex;
    flex-direction : column;
    align-items: center;
    gap : 0.8rem;
    position : absolute;
    bottom : 3rem;
    left : 0;
}

.header_socials::after{
    content: '';
    width: 1px;
    height: 2rem;
    background : var(--color-primary);
}

/*================= SCROLL DOWN ==================*/

.scroll_down{
    position : absolute;
    right : -2.3rem;
    bottom: 5rem;
    transform : rotate(90deg);
    font-weight : 300;
    font-size : 0.9rem;
}

/*================= MEDIA QUERIES (MEDIUM DEVICES) ==================*/
@media screen and (max-width :1024px ){
    header{
        height: 68vh;
    }
}
/*================= MEDIA QUERIES (SMALL) ==================*/
@media screen and (max-width:600px){
    header{
        height: 100vh;
    }
    .header_socials,.scroll_down{
        display : none;
    }
}